import React from 'react';
import { Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';

const DuplicatePostAlert = ({ count, isCollection, collectionId }) => {
    return count && parseInt(count) > 0 ? <Row>
        <Col>
            <div className="d-flex alert alert-danger py-3">
                <div className="d-flex flex-column flex-stack flex-grow-1">
                    <p className="mb-0 text-gray-800 text-center fs-7">{count} Duplicate images detected. To clear them out click on <Link to={`/rule/add?duplicate=true${isCollection ? `&collectionId=${collectionId}` : ``}`} className='fw-bold text-link text-danger text-decoration-none'>'remove'</Link> </p>
                </div>
            </div>
        </Col>
    </Row> : null
}

export default DuplicatePostAlert;